import { Component, OnInit, Injector, ViewChild, TemplateRef, HostListener } from '@angular/core';
import {Router} from '@angular/router';

import { jqxGridComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';

import { MatDialog } from '@angular/material/dialog';

import { ApartmentService } from 'src/app/api/controllers/Apartment';
import { AccountsService } from 'src/app/api/controllers/Accounts';
import { CookieService } from 'ngx-cookie-service';

import * as _ from 'underscore';


@Component({
  selector: 'app-income-sub-ledger',
  templateUrl: './income-sub-ledger.component.html',
  styleUrls: ['./income-sub-ledger.component.scss'],
  providers: [AccountsService]
})
export class IncomeSubLedgerComponent implements OnInit {
 
  isSubLedgerDataLoaded:boolean = false;

  subLedgerData:string = "";
  subLedgerDataList:any;

  totalItems:any;

  totalDueAmount:number = 0;
  netDueAmount:number = 0;

  blockListData:any;

  filterSelected:string = "all";
  singleBlock:string = "Select Tower";
  selectedBlockId:number;

  apartmentBlockId:any;
  apartmentBlockUnitId:any;

  dialogRef:any;

  @ViewChild("viewInvoiceTypeRef", { static: false }) viewInvoiceTypeRef: TemplateRef<any>;

  columnData:any ;
  gridSourceData:any;

  @ViewChild('datagrid', { static: false }) datagrid: jqxGridComponent;

  constructor(
    private router: Router,
    public dialog: MatDialog,
  	private apartmentService:ApartmentService,
  	private accountsService:AccountsService,
  	private cookieService:CookieService
  ) { 
  }

  getDueAmount(value){
  	return value != 'Due' ? true : false;
  }

  getSelected(type){
  	return this.filterSelected == type ? 'selected' : '';
  }

  dialogClose(){
    this.dialogRef.close();
  }

  getPrintParams(event){
    this.datagrid.exportdata(event, 'helpdeskData');
  }

  getAllBlockData(){

  	this.filterSelected = 'all';
    this.singleBlock = "Select Block";
    
    var params = {
      apartmentId: parseInt(this.cookieService.get('apartmentId'))
    }

  	this.accountsService.getIncomeTrackerSubLedgersByApartmentId(params).subscribe((res:any) => {
     
      var subLedgerDataList = res;

      console.log(res);

      this.gridSourceData.localdata = subLedgerDataList;
      this.datagrid.updatebounddata('cells');

      this.calculateTotalAmount(subLedgerDataList);

  	})

  }

  getSingleBlock(block){
  	
  	this.filterSelected = 'single';
  	this.singleBlock = block.apartmentBlockNumber;
  	this.selectedBlockId = block.apartmentBlockId;


  	/*this.accountsService.getIncomeTrackerSubLedgersByApartmentId(parseInt(this.cookieService.get('apartmentId'))).subscribe((res:any) => {

      var subLedgerDataList = res.filter(item => {
        return item.apartmentBlockNumber == this.singleBlock;
      })

      this.gridSourceData.localdata = subLedgerDataList;
      this.datagrid.updatebounddata('cells');

      this.calculateTotalAmount(subLedgerDataList);


  	});*/

  }

  calculateTotalAmount(subLedgerDataList) {

    this.totalItems = subLedgerDataList.length;

      this.netDueAmount = 0;
      this.totalDueAmount = 0;

      _.each(subLedgerDataList, (item, index) => {

        this.netDueAmount = this.netDueAmount + item.due;

        if(item.due >= 0)
        this.totalDueAmount = this.totalDueAmount + item.due;

    });

  }

  isItemsAvailable(){
  	return this.totalItems > 0 ? true : false;
  }

  @HostListener('window:onViewHistory', ['$event.detail'])
  onViewHistory(detail) {

    let dataRecord = this.datagrid.getrowdata(detail.rowId)
    let apartmentBlockUnitId = dataRecord.apartmentBlockUnitId

    this.router.navigateByUrl('/ams/income/view-invoice-history/' + apartmentBlockUnitId);

  }

  @HostListener('window:onShowInvoices', ['$event.detail'])
  onShowInvoices(detail) {

    let ledger = this.datagrid.getrowdata(detail.rowId)

    var invoiceParams = {
      apartmentId: parseInt(this.cookieService.get('apartmentId')),
      apartmentBlockUnitId: ledger.apartmentBlockUnitId
    }

    this.accountsService.getCustInvoicesByApartmentBlockUnitId(invoiceParams).subscribe((res:any) => {
      
      if(res.length == 0){
        ledger.isInvoiceAvailable = false;
      }
      else {
        ledger.isInvoiceAvailable = true;
      }

      this.dialogRef = this.dialog.open(this.viewInvoiceTypeRef, {
        panelClass: 'material',
        disableClose: true,
        data: ledger
      });

    });

  }

  @HostListener('window:onViewPostCollection', ['$event.detail'])
  onViewPostCollection(detail) {

    let dataRecord = this.datagrid.getrowdata(detail.rowId)
    let apartmentBlockUnitId = dataRecord.apartmentBlockUnitId
    
    this.router.navigateByUrl('/ams/income/post-collection/' + apartmentBlockUnitId);

  }

  onGlSearchFilter() {

    if(this.subLedgerData != ""){

      let filtergroup = new jqx.filter();
      let filter_or_operator = 1;
      let filtervalue = this.subLedgerData;
      let filtercondition = 'contains';
      let filterData = filtergroup.createfilter('stringfilter', filtervalue, filtercondition);
      filtergroup.operator = 'or';
      filtergroup.addfilter(filter_or_operator, filterData);

      this.datagrid.showfiltercolumnbackground(false);

      this.columnData.forEach(item => {
        if(item.datafield != 'Actions') {
          this.datagrid.addfilter(item.datafield, filtergroup, true);
        }
      })

      this.datagrid.applyfilters();
      
    }
    else {
      this.datagrid.clearfilters();
    }

  }


  renderColumns(){

    var cellsrenderer = (row, column, value) => {
      return '<div class="jqx-custom-inner-cell">' + value + '</div>';
    }

    var columnrenderer = (value) => {
      return '<div style="padding: 14px">' + value + '</div>';
    }

    this.columnData = [{
         text: 'Primary Contact',
         datafield: 'primayContact',
         width: 180,
         pinned: true,
         cellsrenderer: cellsrenderer,
         renderer: columnrenderer
     },
     {
         text: 'Bill to Party',
         datafield: 'billToParty',
         width: 180,
         cellsrenderer: cellsrenderer,
         renderer: columnrenderer
     },
     {
         text: 'Tower Unit',
         datafield: 'block_Unit',
         width: 120,
         cellsrenderer: cellsrenderer,
         renderer: columnrenderer
     },
     {
         text: 'Due',
         datafield: 'due',
         width: 150,
         cellsrenderer: cellsrenderer,
         renderer: columnrenderer
     },
     {
         text: 'Status',
         datafield: 'due_Indicator',
         width: 150,
         cellsrenderer: (row, column, value) => {
          return '<div class="jqx-custom-inner-cell"><span class="dots rt-1 '+ getClassName(value) +'"></span>' + value + '</div>'
         },
         renderer: columnrenderer
     },
     {
         text: 'Actions',
         cellsalign: 'center', 
         align: 'center',
         minwidth: 120,
         cellclassname: 'action-cell',
         cellsrenderer: (row: number): string => {

           return '<div class="simple-actions">'
                  + '<a href="javascript:void(0)" role="button" data-toggle="dropdown" id="invoiceDropDown" aria-haspopup="true" aria-expanded="false">'
                    + '<span class="action-dot"></span>'
                    + '<span class="action-dot"></span>'
                    + '<span class="action-dot"></span>'
                  + '</a>'
                  + '<div class="dropdown-menu action-menu dropdown-menu-right" aria-labelledby="invoiceDropDown">'
                    + '<a href="javascript:void(0)" onClick="viewHistoryEvent('+ row +')">View History</a>'
                    + '<a href="javascript:void(0)" onClick="showInvoicesEvent('+ row +')">Post Invoice</a>'
                    + '<a href="javascript:void(0)" onClick="viewPostCollectionEvent('+ row +')">Post Collection</a>'
                    + '<a href="javascript:void(0)">Print</a>'
                  + '</div>'
                + '</div>'
         },
         renderer: columnrenderer
     }];


  }


  ngOnInit() {

    let params = {
      apartmentId: parseInt(this.cookieService.get('apartmentId'))
    }

  	this.apartmentService.getApartmentBlockByApartmentId(params).subscribe((res:any) => {
      
      this.blockListData = res;

      var params = {
        apartmentId: parseInt(this.cookieService.get('apartmentId'))
      }

      this.accountsService.getIncomeTrackerSubLedgersByApartmentId(params).subscribe((res:any) => {

        this.isSubLedgerDataLoaded = true;

        this.filterSelected = 'all';
        this.singleBlock = "Select Block";

        var subLedgerDataList = res;

        this.gridSourceData = {
          localdata: subLedgerDataList,
          datatype: "array"
        }

        this.subLedgerDataList = new jqx.dataAdapter(this.gridSourceData);
        this.renderColumns();

        this.calculateTotalAmount(subLedgerDataList);


      });

    });

  }

}

function getClassName(value){
  return value != 'Due' ? 'high' : 'low';
}
(window as any).getClassName = getClassName;

function viewHistoryEvent(row){
  var event = new CustomEvent('onViewHistory', {
    detail: {
        rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).viewHistoryEvent = viewHistoryEvent;


function showInvoicesEvent(row){
  var event = new CustomEvent('onShowInvoices', {
    detail: {
        rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).showInvoicesEvent = showInvoicesEvent;


function viewPostCollectionEvent(row){
  var event = new CustomEvent('onViewPostCollection', {
    detail: {
        rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).viewPostCollectionEvent = viewPostCollectionEvent;
