import { Component, OnInit } from '@angular/core';

import { MatDialog } from '@angular/material/dialog';

import { AccountsService } from 'src/app/api/controllers/Accounts';
import { CookieService } from 'ngx-cookie-service';

import * as _ from 'underscore';
import * as moment from 'moment';

@Component({
  selector: 'app-income-view-receipts',
  templateUrl: './income-view-receipts.component.html',
  styleUrls: ['./income-view-receipts.component.scss']
})
export class IncomeViewReceiptsComponent implements OnInit {
  	
  isReceiptsLoaded:boolean = false;

  receiptsData:string = "";
  receiptsDataList:any;

  ItemStartIndex:any = 0;
  ItemEndIndex:any;
  totalItems:any;
  itemLimit:any = 10;

  unitFieldType:string = "receiptNumber";
  unitOrder:boolean = true;

  isMobile:boolean = false;

  selectedInput:string = "";
  columnField: any = {}

  constructor(
    public dialog: MatDialog,
  	private accountsService:AccountsService,
  	private cookieService:CookieService
  ) { }


  getIndexParams(event){
    this.ItemStartIndex = event.ItemStartIndex;
    this.ItemEndIndex = event.ItemEndIndex;
    this.itemLimit = event.itemLimit;
  }

  sortUnitData(type){
    this.unitFieldType = type;
    this.unitOrder = !this.unitOrder;
  }

  getFieldOrderBy(type) {
    if(this.unitFieldType == type){
      return this.unitOrder ? 'asc' : 'desc';
    }
    else return '';
  }

  getEmailStatus(value){
  	if(value){
  		return 'Y'
  	}
  	else {
  		return 'N'
  	}
  }

  getDate(receipt, date){

   this.receiptsDataList.map(obj=> {
      if(obj.receiptNumber == receipt.receiptNumber){
        obj.paymentDateFormat = moment(date).format("DD/MM/YYYY");
      }
   });

  	return moment(date).format("DD/MM/YYYY"); 
  }

  isMobileView() {
    return window.innerWidth <= 767 ? 'table-responsive' : '';
  }

  isItemsAvailable(){
  	return this.totalItems > 0 ? true : false;
  }

  isNoItemsAvailable(){
  	return this.totalItems == 0 ? true : false;
  }

  setActive(item){
  	return ''
  }

  selectColInput(value){
    this.selectedInput = value;
  }

  onSelectChange(event, type, name){

    if(!_.isEmpty(event)){
      this.selectedInput = type;
      this.columnField[type] = event[name];
    }
    else {
       this.columnField = {};
    }
    
  }

  onDateChange(event, type){

    if(event != null){
      this.selectedInput = type;
      this.columnField[type] = moment(event).format("DD/MM/YYYY");
    }
    else {
      this.columnField = {};
    }
    
  }

  ngOnInit() {

    let params = {
      apartmentId: parseInt(this.cookieService.get('apartmentId'))
    }

  	this.accountsService.getIncomeTrackerReceiptsByApartmentId(params).subscribe((res:any) => {

  		this.receiptsDataList = res;

  		this.totalItems = this.receiptsDataList.length;

  		if(this.totalItems>this.itemLimit){
			  this.ItemEndIndex = this.itemLimit;
      }
      else {
        this.ItemEndIndex = this.totalItems;
      }

		  this.isReceiptsLoaded = true;

  	}, error => {
    	console.log(error);
    });

  }

}
