import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AuthGuard } from '../.././auth.guard';

import { IncomeTrackerComponent } from './income-tracker.component';
import { IncomeSubLedgerComponent } from './components/income-sub-ledger/income-sub-ledger.component';
import { IncomeViewReceiptsComponent } from './components/income-view-receipts/income-view-receipts.component';
import { IncomeViewCreditComponent } from './components/income-view-credit/income-view-credit.component';
import { IncomeViewInvoiceComponent } from './components/income-view-invoice/income-view-invoice.component';
import { IncomeViewInvoiceHistoryComponent } from './components/income-view-invoice-history/income-view-invoice-history.component';
import { IncomeViewDefaultersComponent } from './components/income-view-defaulters/income-view-defaulters.component';
import { IncomePostInvoiceComponent } from './components/income-post-invoice/income-post-invoice.component';
import { IncomePostMultiInvoiceComponent } from './components/income-post-multi-invoice/income-post-multi-invoice.component';
import { IncomePostMultiInvoiceFieldsComponent } from './components/income-post-multi-invoice-fields/income-post-multi-invoice-fields.component';
import { IncomePostCollectionComponent } from './components/income-post-collection/income-post-collection.component';
import { IncomePayInvoiceComponent } from './components/income-pay-invoice/income-pay-invoice.component';
import { IncomeTrackerAgeingComponent } from './components/income-tracker-ageing/income-tracker-ageing.component';
import { IncomeAddCreditComponent } from './components/income-add-credit/income-add-credit.component';
import { IncomeInvoiceReverseComponent } from './components/income-invoice-reverse/income-invoice-reverse.component';
import { IncomeTrackerReportsComponent } from './components/income-tracker-reports/income-tracker-reports.component';
import { IncomeTrackerSetupComponent } from './components/income-tracker-setup/income-tracker-setup.component';
import { IncomeTrackerReportsCustomerComponent } from './components/income-tracker-reports/income-tracker-reports-customer/income-tracker-reports-customer.component';
import { IncomeTrackerReportsVendorComponent } from './components/income-tracker-reports/income-tracker-reports-vendor/income-tracker-reports-vendor.component';

const routes: Routes = [
	
	{ path: '', redirectTo: 'subledger', pathMatch: 'full'},
	{ path: 'settings', component: IncomeTrackerSetupComponent},
	{ path: 'subledger', component: IncomeSubLedgerComponent},
	{ path: 'view-invoice-history/:id', component: IncomeViewInvoiceHistoryComponent},
	{ path: 'all-invoices', component: IncomeViewInvoiceComponent},
	//{ path: 'post-invoice/:id/:type', component: IncomePostInvoiceComponent},
	{ path: 'post-multi-invoice/:id/:type', component: IncomePostMultiInvoiceComponent},
	//{ path: 'edit-invoice/:id', component: IncomePostInvoiceComponent},
	{ path: 'edit-invoice/:id/:invoiceid', component: IncomePostMultiInvoiceComponent},
	{ path: 'post-collection/:id', component: IncomePayInvoiceComponent},
	{ path: 'receipts', component: IncomeViewReceiptsComponent},
	{ path: 'creditnote', component: IncomeViewCreditComponent},
	{ path: 'add-credit/:id/:type', component: IncomeAddCreditComponent},
	{ path: 'edit-credit/:id', component: IncomeAddCreditComponent},
	{ path: 'defaulters', component: IncomeViewDefaultersComponent },
	{ path: 'ageing', component: IncomeTrackerAgeingComponent},
	{ path: 'reports', component: IncomeTrackerReportsComponent},
	{ path: 'reports/customer', component: IncomeTrackerReportsCustomerComponent},
	{ path: 'reports/vendor', component: IncomeTrackerReportsVendorComponent}, 
	{ path: '**', redirectTo: 'subledger', pathMatch: 'full'}

];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class IncomeTrackerRoutingModule { }
