import { Component, OnInit } from '@angular/core';
import { Router, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';

import { ApartmentService } from 'src/app/api/controllers/Apartment';
import { AccountsService } from 'src/app/api/controllers/Accounts';
import { SharedService } from '../../../../shared/services/shared.service';
import { CookieService } from 'ngx-cookie-service';

import * as _ from 'underscore';

@Component({
  selector: 'app-income-add-credit',
  templateUrl: './income-add-credit.component.html',
  styleUrls: ['./income-add-credit.component.scss']
})
export class IncomeAddCreditComponent implements OnInit {
  
  isEditCredit:boolean = false;
  isCreditSubmitted:boolean = true;

  blockData:any;
  blockUnitData:any;
  invoiceData:any;
  
  unitTypeId:any;

  block:any;

  unitListData:any;

  apartmentBlockId:any;
  apartmentBlockUnitId:any;
  custInvoiceId:any;

  isBlockSelected:boolean = false;
  isUnitSelected:boolean = false;
  isAmountAdded:boolean = false;

  isError:boolean = false;
  alertMessage:string = "";

  credit:any;

  accountDataList:any;
  isAccountDataLoaded:boolean = false;

  constructor(
  	private router: Router,
    private route: ActivatedRoute,
  	private apartmentService:ApartmentService,
  	private accountsService:AccountsService,
  	private sharedService:SharedService,
  	private cookieService:CookieService
  ) { }

  showUnitData(blockId){
  	
  	this.apartmentService.getApartmentBlockUnitByBlockId(blockId).subscribe((res:any) => {
  		this.unitListData = res;
      this.apartmentBlockUnitId = "";
  		this.isBlockSelected = true;
      this.isUnitSelected = false;
      this.isAmountAdded = false;
  	});

  }

  showInvoiceData(unitId){

  	var params = {
  		apartmentId: parseInt(this.cookieService.get('apartmentId')),
  		apartmentBlockUnitId: unitId
  	}

  	this.accountsService.getCustInvoicesByApartmentBlockUnitId(params).subscribe((res:any) => {
  		this.invoiceData = res;
      this.credit.invoiceId = "";
  		this.isUnitSelected = true;
  	});

  }

  getInvoice(invoiceId){

  	var data = this.invoiceData.filter(item => {
  		return item.custInvoiceId == invoiceId
  	});

  	var params = {
  		apartmentId: parseInt(this.cookieService.get('apartmentId')),
  		invoiceId: invoiceId
  	}

  	this.accountsService.getCollectionInvoiceByInvoiceId(params).subscribe((res:any) => {

  		if(res === undefined || res.length == 0){
        if(this.route.params['value'].type == undefined)
	      this.credit.collectionId = null;
        else
          this.credit.collectionId = 0;
	    }

	    else {
	      this.credit.collectionId = res[0].collectionId;
	    }
  		
  	});

  	this.credit.amount = data[0].custInvoiceAmount;

  	this.isAmountAdded = true;
  }

  toggleEmail(){
  	this.credit.isEmailSent = true;
  }

  toggleSms(){
  	this.credit.isSmssent = !this.credit.isSmssent;
  }

  setActive(){
    return ''
  }
  	
  getUnitData(id){
  	this.apartmentService.getApartmentBlockUnitById(id).subscribe((res:any) => {
  		
  		this.unitListData = res;
  		this.apartmentBlockId = "" + res[0].apartmentBlockId;

  		this.apartmentService.getApartmentBlockUnitByBlockId(res[0].apartmentBlockId).subscribe((res:any) => {
  			this.unitListData = res;
  		});

  	})
  }

  getInvoiceData(id){

  }

  submitAddCreditNoteForm(form){

  	if(!this.isEditCredit){

  		let details = {
  
			"apartmentId": parseInt(this.cookieService.get('apartmentId')),
			"amount": parseInt(this.credit.amount),
			"collectionId": 0,
			"invoiceId": parseInt(this.credit.invoiceId),
			"isEmailSent": this.credit.isEmailSent,
			"isSmssent": this.credit.isSmssent,
			"comment": this.credit.comment,
			"isActive": true,
			"insertedBy": parseInt(this.cookieService.get('userId')),
			"insertedOn": this.credit.insertedOn,
			"updatedBy": 0,
			"updatedOn": this.credit.insertedOn,
			"transReference1": "string",
			"transReference2": "string",
			"glAcctIndicator": "Income",
			"apartmentBlockUnitId": parseInt(this.route.params['value'].id)

  		}

		let params = {
			creditNote: details
		}

  		this.accountsService.addCreditNote(params).subscribe((res:any) => {

	  		if(res.message){

		      this.isCreditSubmitted = true;
		      this.sharedService.setAlertMessage("Credit Note added successfully");
		      this.router.navigate(['ams/income/creditnote']);

		    }
		    else {

		      this.isCreditSubmitted = true;
		      this.isError = true;
		      this.alertMessage = res.errorMessage;

		    }


	  	},
	    error => {
	      this.isCreditSubmitted = true;
	      this.isError = true;
	      this.alertMessage = "Some error occured";
	    });

  	}

  	else {

  		let details = {

  		"creditNoteId": this.credit.creditNoteId,
			"apartmentId": parseInt(this.cookieService.get('apartmentId')),
			"amount": parseInt(this.credit.amount),
			"collectionId": null,
			"invoiceId": parseInt(this.credit.invoiceId),
			"isEmailSent": this.credit.isEmailSent,
			"isSmssent": this.credit.isSmssent,
			"comment": this.credit.comment,
			"isActive": true,
			"insertedBy": parseInt(this.cookieService.get('userId')),
			"insertedOn": this.credit.insertedOn,
			"updatedBy": parseInt(this.cookieService.get('userId')),
			"updatedOn": this.credit.insertedOn,
			"transReference1": "string",
			"transReference2": "string",
			"glAcctIndicator": "Income",
			"apartmentBlockUnitId": parseInt(this.apartmentBlockUnitId)

  		}

		let params = {
			creditNote: details
		}

  		this.accountsService.updateCreditNote(params).subscribe((res:any) => {

	  		if(res.message){

		      this.isCreditSubmitted = true;
		      this.sharedService.setAlertMessage("Credit Note updated successfully");
		      this.router.navigate(['ams/income/creditnote']);

		    }
		    else {

		      this.isCreditSubmitted = true;
		      this.isError = true;
		      this.alertMessage = res.errorMessage;

		    }


	  	},
	    error => {
	      this.isCreditSubmitted = true;
	      this.isError = true;
	      this.alertMessage = "Some error occured";
	    });

  	}


  }



  ngOnInit() {

  	this.credit = {};
  	this.credit.invoiceId = "";
  	this.credit.comment = "";

  	this.credit.isEmailSent = false;
  	this.credit.isSmssent = false;

  	this.apartmentBlockId = "";
  	this.apartmentBlockUnitId = "";


  	 if(this.route.params['value'].id != undefined && this.route.params['value'].type == undefined) {
  	 	

  	 	var params = {
  			apartmentId: parseInt(this.cookieService.get('apartmentId')),
  			creditNoteId: this.route.params['value'].id
  		}

  	 	this.accountsService.getCreditNoteById(params).subscribe((res:any) => {
        	
        	this.credit = res[0];

          if(this.credit.comment == 'string'){
            this.credit.comment = "";
          }

        	this.apartmentBlockUnitId = "" + this.credit.apartmentBlockUnitId;
        	this.getUnitData(this.credit.apartmentBlockUnitId);

          var invoiceParams = {
            apartmentId: parseInt(this.cookieService.get('apartmentId')),
            apartmentBlockUnitId: this.credit.apartmentBlockUnitId
          }

          this.accountsService.getCustInvoicesByApartmentBlockUnitId(invoiceParams).subscribe((res:any) => {
            this.invoiceData = res;
          });

		  this.isEditCredit = true;
		  
		  let acountListParams = {
			  apartmentId: parseInt(this.cookieService.get('apartmentId'))
		  }

          this.accountsService.getIncomeTrackerSubLedgersByApartmentId(acountListParams).subscribe((res:any) => {

            this.accountDataList = res.filter(item => {
              return item.apartmentBlockUnitId == this.credit.apartmentBlockUnitId;
            });


            this.isAccountDataLoaded = true;


         });

		},
		error => {

		});

  	}
    else {
	  
	 let accountListParams = {
		apartmentId: parseInt(this.cookieService.get('apartmentId'))
	 }

      this.accountsService.getIncomeTrackerSubLedgersByApartmentId(accountListParams).subscribe((res:any) => {

        this.accountDataList = res.filter(item => {
          return item.apartmentBlockUnitId == this.route.params['value'].id;
        });

        this.isAccountDataLoaded = true;


      });


      this.apartmentBlockUnitId = "" + this.route.params['value'].id;
      this.getUnitData(this.route.params['value'].id);

      var invoiceParams = {
        apartmentId: parseInt(this.cookieService.get('apartmentId')),
        apartmentBlockUnitId: this.apartmentBlockUnitId
      }

      this.accountsService.getCustInvoicesByApartmentBlockUnitId(invoiceParams).subscribe((res:any) => {
        this.invoiceData = res;
        this.isUnitSelected = true;
      });

      this.isBlockSelected = true;
      
    }

	let blockListParams = {
		apartmentId: parseInt(this.cookieService.get('apartmentId'))
	}

  	this.apartmentService.getApartmentBlockByApartmentId(blockListParams).subscribe((res:any) => {
      this.blockData = res;
    });

    

  }

}
