import { Component, OnInit, AfterViewInit, ViewChild, HostListener, TemplateRef } from '@angular/core';
import { Router, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';

import { MatDialog } from '@angular/material/dialog';

import { jqxGridComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';
import { jqxWindowComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxwindow';
import { jqxPopoverComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxpopover';

import { UserService } from 'src/app/api/controllers/User';
import { AccountsService } from 'src/app/api/controllers/Accounts';
import { SharedService } from '../../../../shared/services/shared.service';
import { ConstantsService } from '../../../../shared/services/constants.service';
import { CookieService } from 'ngx-cookie-service';


import * as _ from 'underscore';
import * as moment from 'moment';

@Component({
  selector: 'app-income-view-invoice',
  templateUrl: './income-view-invoice.component.html',
  styleUrls: ['./income-view-invoice.component.scss']
})
export class IncomeViewInvoiceComponent implements OnInit, AfterViewInit {

  isInvoiceDataLoaded:boolean = false;

  invoiceData:string = "";
  invoiceDataList:any;

  invoice:any;

  userDataList:any;
  glAccountListData:any;

  ItemStartIndex:any = 0;
  ItemEndIndex:any;
  totalItems:any;
  itemLimit:any = 10;

  unitFieldType:string = "custInvoiceId";
  unitOrder:boolean = true;

  isMobile:boolean = false;

  glAccountIndicator:any = "Income";

  selectedInput:string = "";
  columnField: any = {}

  columnData:any ;
  gridSourceData:any;
  reversePopOverSelector:any;

  popoverSettings: jqwidgets.PopoverOptions;

  @ViewChild('datagrid', { static: false }) datagrid: jqxGridComponent;
  @ViewChild('reversePopWindow', { static: false }) reversePopWindow: jqxWindowComponent;
  @ViewChild('reversePopOver', { static: false }) reversePopOver: jqxPopoverComponent;
  @ViewChild("viewInvoiceDetailsRef", { static: false }) viewInvoiceDetailsRef: TemplateRef<any>;

  constructor(
    public dialog: MatDialog,
  	private router: Router,
    private route: ActivatedRoute,
    private userService:UserService,
  	private accountsService:AccountsService,
  	private sharedService:SharedService,
    private constantsService:ConstantsService,
  	private cookieService:CookieService
  ) { }


  getDueAmount(due, paid){
    return (due-paid) > 0 ? 'high' : 'low';
  }

  stopPropagation(event: Event){
    event.stopPropagation();
  }

  onGlSearchFilter() {

    if(this.invoiceData != ""){

      let filtergroup = new jqx.filter();
      let filter_or_operator = 1;
      let filtervalue = this.invoiceData;
      let filtercondition = 'contains';
      let filterData = filtergroup.createfilter('stringfilter', filtervalue, filtercondition);
      filtergroup.operator = 'or';
      filtergroup.addfilter(filter_or_operator, filterData);

      this.datagrid.showfiltercolumnbackground(false);

      this.columnData.forEach(item => {
        if(item.datafield != 'Actions') {
          this.datagrid.addfilter(item.datafield, filtergroup, true);
        }
      })

      this.datagrid.applyfilters();
      
    }
    else {
      this.datagrid.clearfilters();
    }

  }


  @HostListener('window:onDownloadInvoice', ['$event.detail'])
  onDownloadInvoice(detail) {
    
    let dataRecord = this.datagrid.getrowdata(detail.rowId);

    var userData = {};

    this.dialog.open(this.viewInvoiceDetailsRef, {
      panelClass: 'material',
      disableClose: true,
      data: userData
    });
  
  }

  @HostListener('window:onEditInvoice', ['$event.detail'])
  onEditInvoice(detail) {
    let dataRecord = this.datagrid.getrowdata(detail.rowId)

    let custInvoiceId = dataRecord.custInvoiceId
    let apartmentBlockUnitId = dataRecord.apartmentBlockUnitId
    this.router.navigateByUrl('/ams/income/edit-invoice/' + apartmentBlockUnitId + '/' + custInvoiceId);
  }

  @HostListener('window:onEditReverse', ['$event.detail'])
  onEditReverse(detail) {

    let dataRecord = this.datagrid.getrowdata(detail.rowId)
    this.invoice = dataRecord;
    this.reversePopOverSelector = '#'+this.invoice.custInvoiceId;    

    setTimeout(() => {
      this.reversePopOver.createComponent();
      this.reversePopOver.open();
    }, 100);

  }

  @HostListener('window:onUpdateInvoice', ['$event.detail'])
  onUpdateInvoice(detail) {

    var invoice = this.datagrid.getrowdata(detail.rowId);
    this.updateInvoiceItem(invoice, detail.column);

  }

  updateInvoiceItem(invoice, type){

      if(type == 'email'){
        if(invoice.isEmailSent) 
          invoice.isEmailSent = false;
        else
          invoice.isEmailSent = true;
      }
      else {
        if(invoice.isSmssent) 
          invoice.isSmssent = false;
        else
          invoice.isSmssent = true;
      }

      let details = {
          
          "custInvoiceId": invoice.custInvoiceId,
          "apartmentId": parseInt(this.cookieService.get('apartmentId')),
          "apartmentBlockUnitId": invoice.apartmentBlockUnitId,
          "custInvoiceAmount": parseInt(invoice.custInvoiceAmount),
          "custInvoiceDate": new Date().toISOString(),
          "dueDate": invoice.dueDate,
          "tax1": 0,
          "tax2": 0,
          "tax3": 0,
          "isTax": invoice.isTax,
          "isRecurCustInvoice": invoice.isRecurCustInvoice,
          "isEmailSent": invoice.isEmailSent,
          "isSmssent": invoice.isSmssent,
          "custInvoiceStatusId": 1,
          "postedBy": parseInt(this.cookieService.get('userId')),
          "postedOn": new Date().toISOString(),
          "billToPay": "string",
          "comments": invoice.comments || "",
          "penaltyAmount": 0,
          "penaltyComment": "string",
          "isActive": true,
          "insertedBy": parseInt(this.cookieService.get('userId')),
          "insertedOn": new Date().toISOString(),
          "updatedBy": parseInt(this.cookieService.get('userId')),
          "updatedOn": new Date().toISOString(),
          "transReference1": "string",
          "transReference2": "string",
          "invoiceGLAccounts": [
            { 
              "invoiceGlaccountId": invoice.invoiceGLAccounts[0].invoiceGlaccountId, 
              "apartmentId": parseInt(this.cookieService.get('apartmentId')),
              "invoiceId": 0,
              "glaccountId": invoice.invoiceGLAccounts[0].glaccountId,
              "comments": "string",
              "isActive": true,
              "insertedBy": parseInt(this.cookieService.get('userId')),
              "insertedOn": new Date().toISOString(),
              "updatedBy": parseInt(this.cookieService.get('userId')),
              "updatedOn": new Date().toISOString(),
              "amount": invoice.custInvoiceAmount
            }
          ]
      }

      let params = {
        invoice: details
      }

      this.accountsService.updateCustInvoice(params).subscribe((res:any) => {

        if(res.message){
          this.sharedService.setAlertMessage("Invoice updated successfully");
       }
       else {
          this.isInvoiceDataLoaded = true;
       }

      },
      error => {
          this.isInvoiceDataLoaded = true;
      });

  }

  ngOnInit() {


  	var accountParams = {
        apartmentId: parseInt(this.cookieService.get('apartmentId')),
        groupId: 3
    }

    var cellsrenderer = (row, column, value) => {
      return '<div class="jqx-custom-inner-cell">' + value + '</div>';
    }

    var columnrenderer = (value) => {
      return '<div style="padding: 14px">' + value + '</div>';
    }

    this.accountsService.getAllGlAccounts().subscribe((res:any) => {

        this.glAccountListData = res.filter(item => {
          return item.isActive && parseInt(this.cookieService.get('apartmentId')) && item.indicator == this.glAccountIndicator;
        });

    });

    let params = {
      apartmentId: parseInt(this.cookieService.get('apartmentId'))
    }

  	this.accountsService.getCustInvoicesByApartmentId(params).subscribe((res:any) => {

      var invoiceDataList = res;
      
      _.each(invoiceDataList, item => {
        item.towerUnit = item.apartmentBlockNumber + ' ' + item.apartmentBlockUnitNumber;
      })

      this.invoice = invoiceDataList[0];
      this.reversePopOverSelector = '#'+this.invoice.custInvoiceId;

      this.gridSourceData = {
        localdata: invoiceDataList,
        datatype: "array"
      }

      this.invoiceDataList = new jqx.dataAdapter(this.gridSourceData);

      /*this.getSourceDataAdapter = (datafield: string): any => {
        let dataAdapter = new jqx.dataAdapter(this.gridSourceData, { uniqueDataFields: [datafield] });
        return dataAdapter;
      }*/

      this.totalItems = this.invoiceDataList.length;
      
      this.columnData = [{
        text: 'Invoice Id',
        datafield: 'custInvoiceId',
        width: 120,
        pinned: true,
        cellsrenderer: (row, column, value): string => {
          return '<div class="jqx-custom-inner-cell"><a href="javascript:void(0)" class="mr-2 t-decor" onClick="downloadInvoiceEvent('+ row +')">'+ value +'</a></div>'
        },
        renderer: columnrenderer
      },
      {
          text: 'Invoice Date',
          datafield: 'postedOn',
          minwidth: 120,
          cellsrenderer: (row, column, value): string => {
            return '<div class="jqx-custom-inner-cell">' + moment(value).format(this.constantsService.dateFormat) + '</div>'
          },
          renderer: columnrenderer
      },
      {
          text: 'Posted By',
          datafield: 'postedByName',
          minwidth: 120,
          cellsrenderer: cellsrenderer,
          renderer: columnrenderer
      },
      {
          text: 'Tower Unit',
          datafield: 'block_Unit',
          minwidth: 120,
          cellsrenderer: cellsrenderer,
          renderer: columnrenderer
      },
      {
          text: 'Due',
          datafield: 'custInvoiceAmount',
          minwidth: 150,
          cellsrenderer: cellsrenderer,
          renderer: columnrenderer
      },
      {
          text: 'Sent Email',
          datafield: 'isEmailSent',
          minwidth: 120,
          cellsrenderer: (row: number, column: string, value: boolean): string => {

          var chkedProperty;
          if(value){
            chkedProperty = 'checked="true"';
          }
          else {
            chkedProperty = '';
          }

            return '<div class="jqx-custom-inner-cell">'
                      + '<label class="switch float-left">'
                        + '<input type="checkbox" id="invoiceEmail" name="invoiceEmail"'+ chkedProperty +'>'
                        + '<div class="slider round" onClick="updateInvoiceEvent('+ row + ', \'email\')"></div>'
                      + '</label>'
                + '</div>'
          },
          renderer: columnrenderer
      },
      {
          text: 'SMS/Email',
          datafield: 'isSmssent',
          minwidth: 120,
          cellsrenderer: (row: number, column, value: boolean): string => {

          var chkedProperty;
          if(value){
            chkedProperty = 'checked="true"';
          }
          else {
            chkedProperty = '';
          }

            return '<div class="jqx-custom-inner-cell">'
                      + '<label class="switch float-left">'
                        + '<input type="checkbox" id="invoiceSMS" name="invoiceSMS"'+ chkedProperty +'>'
                        + '<div class="slider round" onClick="updateInvoiceEvent('+ row + ', \'sms\')"></div>'
                      + '</label>'
                + '</div>'
          },
          renderer: columnrenderer
      },
      {
          text: 'Actions',
          cellsalign: 'center', 
          align: 'center',
          minwidth: 120,
          cellsrenderer: (row): string => {

          let elemId = invoiceDataList[row].custInvoiceId;
            return '<div class="simple-actions"><a href="javascript:void(0)" class="mr-2" onClick="editInvoiceEvent('+ row +')"><i class="fa fa-pencil icon edit" aria-hidden="true"></i></a><a href="javascript:void(0)" class="mr-2" id="'+ elemId +'" onClick="editReverseEvent('+ row +')"><i class="fa fa-repeat icon delete" aria-hidden="true"></i></a></div>'
          },
          renderer: columnrenderer
      }];

      let params = {
        apartmentId: parseInt(this.cookieService.get('apartmentId'))
      }

  		this.userService.getAllUsersByApartmentId(params).subscribe((res:any) => {

  			this.userDataList = res;
        this.isInvoiceDataLoaded = true;

  		}, error => {

  		});

  	})

  }

  ngAfterViewInit() {

  }

}



function downloadInvoiceEvent(row){
  var event = new CustomEvent('onDownloadInvoice', {
    detail: {
        rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).downloadInvoiceEvent = downloadInvoiceEvent;

function editInvoiceEvent(row){
  var event = new CustomEvent('onEditInvoice', {
    detail: {
        rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).editInvoiceEvent = editInvoiceEvent;


function editReverseEvent(row){
  
  var event = new CustomEvent('onEditReverse', {
    detail: {
        rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).editReverseEvent = editReverseEvent;


function updateInvoiceEvent(row, type){

  var event = new CustomEvent('onUpdateInvoice', {
    detail: {
        rowId: row,
        column: type
    }
  })
  window.dispatchEvent(event);
}
(window as any).updateInvoiceEvent = updateInvoiceEvent;

