import { Component, OnInit, Injector } from '@angular/core';

import { AccountsService } from 'src/app/api/controllers/Accounts';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { SharedService } from '../../../../shared/services/shared.service';
import { CookieService } from 'ngx-cookie-service';


@Component({
  selector: 'app-income-tracker-setup',
  templateUrl: './income-tracker-setup.component.html',
  styleUrls: ['./income-tracker-setup.component.scss']
})
export class IncomeTrackerSetupComponent implements OnInit {
  
	isSettingsDataLoaded:boolean = true;
	isPenaltyDataLoaded:boolean = true;

	isError:boolean = false;
	errorMessage:boolean = false;

	glAccountListData:any;
	glAccountIndicator:any = "Income";

	currencyTypeDataList:any;
	paymentInvoiceDataList:any
	customerReconciliationAccountDataList:any;
	vatPecentageDataList:any;

	penaltyDataList:any;
	lastPaymentDataList:any;
	penaltyPercentageDataList:any;
	gracePeriodInvoiceDataList:any;

	setting:any;
	penalty:any;
	isEdit:boolean = false;
	isPenaltyEdit:boolean = false;

  constructor(
  	private accountsService:AccountsService,
  	private lookupService: LookupService,
    private sharedService:SharedService,
    private cookieService:CookieService
  ) { }


  submitIncomeSettingsForm(form){
	this.setting.currencyId = this.setting.currencyId;
	this.setting.glAccountId = this.setting.glAccountId;
	this.setting.paymentTerm = this.setting.paymentTerm;
	this.setting.vatPercentage = this.setting.vatPercentage;
    this.isEdit = false;
    this.sharedService.setAlertMessage("updated successfully");
     
  }

  submitIncomePenalitySettingsForm(form){
	this.penalty.penaltyTypeId = "Daily Interest";
	this.penalty.latePaymentId = "124";
	this.penalty.penaltyPercentage = "10";
	this.penalty.gracePeriodInvoiceId = "50";
	this.isPenaltyEdit = false;
	this.sharedService.setAlertMessage("updated successfully");
  }

  ngOnInit() {

  	this.setting = {};

  	this.setting.currencyId = "dollor";
  	this.setting.glAccountId = "1234";
  	this.setting.paymentTerm = "abcd";
  	this.setting.vatPercentage = "10";

  	this.penalty = {};

  	this.penalty.penaltyTypeId = "Daily Interest";
  	this.penalty.latePaymentId = "124";
  	this.penalty.penaltyPercentage = "10";
  	this.penalty.gracePeriodInvoiceId = "50";

	let params = {
		LookupTypeId: 88
	}

  	//currency type
  	this.lookupService.getLookupValueByLookupTypeId(params).subscribe((res:any) => {

  		this.currencyTypeDataList = res.filter(item => {
  			return item.isActive;
  		});

  	});

  	this.accountsService.getAllGlAccounts().subscribe((res:any) => {

      this.glAccountListData = res.filter(item => {
        return item.isActive && parseInt(this.cookieService.get('apartmentId')) && item.indicator == this.glAccountIndicator;
      });

	});
	
	let invoiceListParams = {
		LookupTypeId: 88
	}

  	//payment term
  	this.lookupService.getLookupValueByLookupTypeId(invoiceListParams).subscribe((res:any) => {

  		this.paymentInvoiceDataList = res.filter(item => {
  			return item.isActive;
  		});

  	});

	let vatListParams = {
		LookupTypeId: 77
	}

  	//VAT
  	this.lookupService.getLookupValueByLookupTypeId(vatListParams).subscribe((res:any) => {

  		this.vatPecentageDataList = res.filter(item => {
  			return item.isActive;
  		});

  	});

	let penalityListParams = {
		LookupTypeId: 80
	}
	
  	//penalty
  	this.lookupService.getLookupValueByLookupTypeId(penalityListParams).subscribe((res:any) => {

  		this.penaltyDataList = res.filter(item => {
  			return item.isActive;
  		});

	});
	  
	let lateListParams = {
		LookupTypeId: 81
	}

  	//late payment
  	this.lookupService.getLookupValueByLookupTypeId(lateListParams).subscribe((res:any) => {

  		this.lastPaymentDataList = res.filter(item => {
  			return item.isActive;
  		});

  	});

	let penalityParams = {
		LookupTypeId: 82
	}

  	//% Penalty percentage
  	this.lookupService.getLookupValueByLookupTypeId(penalityParams).subscribe((res:any) => {

  		this.penaltyPercentageDataList = res.filter(item => {
  			return item.isActive;
  		});

  	});
	  
	let graceListParams = {
		LookupTypeId: 83
	}
	  
  	//% grace period for invoice
  	this.lookupService.getLookupValueByLookupTypeId(graceListParams).subscribe((res:any) => {

  		this.gracePeriodInvoiceDataList = res.filter(item => {
  			return item.isActive;
  		});

  	});

  }

  showEditForm(){
	this.isEdit = !this.isEdit;
}

showPenaltyEditForm(){
	this.isPenaltyEdit = !this.isPenaltyEdit;
}
}
